@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Fascinate&display=swap");

:global {
  html,
  body,
  #root {
    height: 100%;
  }

  body {
    margin: 0;
    padding: 0;
    font-family: Roboto, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
}

.main {
  min-height: 100%;
  position: relative;
}

@media (max-width: 480px) {
  .header {
    font-size: 2.5rem;
  }

  .main {
    padding: 1rem;
  }

  .sub-title {
    font-size: 1rem;
  }
}

@media (min-width: 481px) {
  .header {
    font-size: 3rem;
  }

  .main {
    padding: 2rem 1rem 5rem 1rem;
    margin: 0rem 0.5rem 5rem 0.5rem;
  }
  .sub-title {
    font-size: 1.5rem;
  }
}

@media (min-width: 781px) {
  .header {
    font-size: 4rem;
  }
  .main {
    margin: 0rem 0.5rem 5rem 0.5rem;
    padding: 2rem 5rem 5rem 5rem;
  }
  .sub-title {
    font-size: 2rem;
  }
}

.header {
  width:100%;
  height: fit-content;
  padding:1rem;
  margin-bottom:2rem;
  position: relative;
  left: 50%;
  transform: translateX(-50%) rotateZ(-1deg);

  text-align: center;
  box-shadow: 0.5rem 0.5rem 0.5rem rgb(0, 52, 131);
  background-color: rgb(0, 102, 255);
  color: white;

  font-family: "Source Sans Pro", sans-serif;

}

.header-title {
  font-family: Fascinate, sans-serif;
  text-align: center;
  word-break: break-word;
  width:100%;
}

.sub-title {
  text-transform: uppercase;
  font-family: "Sans Source Pro", sans-serif;
  text-align: center;
  width:100%;
}

.title2 {
  position: absolute;
  left: 50%;
  transform: translate(-49%, 3%);
  color: rgb(0, 7, 100);
  z-index: -1;
}

.sub-title-char {
  box-shadow: 0.2rem 0.2rem 0.05rem rgb(33, 0, 59);
  margin: 0 0.1rem;
  padding: 0 0.25rem;

  background-color: rgb(140, 0, 255);
  color: white;
}

.under-contruction > div {
  width: 75%;
  transform: rotateZ(1deg);
  box-shadow: 0.2rem 0.2rem 0.05rem rgb(173, 98, 0);
  background-color: rgb(255, 145, 0);
  color: white;
  display: grid;
  grid-template-columns: 2fr 1fr;
}

.under-contruction > div span {
  text-align: right;
}

.bottom {
  position: relative;
  padding: 1rem;
  background-color: rgb(255, 68, 0);
  box-shadow: 0.1rem 0.1em 0.05rem rgb(173, 0, 0);
  color: white;
  border-radius: 0.5rem;
  font-size: 2rem;
  font-weight: 800;
  border: none;
  left: 50%;
  transform: translateX(-50%);
  margin-bottom: 5rem;
  display: inline;
  width: fit-content;
  cursor: pointer;
  outline: none;
  text-transform: uppercase;
}

.bottom:active {
  background-color: rgb(255, 68, 0);
  box-shadow: inset 0.5rem 0.5rem 0.05rem rgb(173, 0, 0);
}

.mt-1 {
  margin-top: 1rem;
}

.documentation {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 1rem;
}

.description {
  text-transform: uppercase;
  text-align: center;
  color: #fff;
}

.playButton {
  width: fit-content;
}

.uppercase {
  text-transform: uppercase;
}
